<template>
  <div class="container">
    <div class="seachbox">   
        <input class="inputText fl" type="text" placeholder=" ISBN" v-model="bookISBN">      
        <input v-on:click="seachBook" class="seachBtn fl" type="button" value="搜索">
    </div>

    <div>      
      <input v-on:click="miashiti" class="mianshiti" type="button" value="前端面试题">      
    </div>

    <div class="book-msg">
      <h3 class="book-name">{{bookName}}</h3>
      <div class="book-base-msg">       
        <img class="bookimage col-md-4 fl" v-bind:src="imgUrl" alt="找不到图片">        
        <div class="bookmsglist col-md-8 fl">
          <div>作者:<a href="">{{author}}</a></div>
          <div>出版社:{{publisher}}</div>
          <div>出品方:<a href="">{{publisher2}}</a></div>
          <div>原作名:{{origin_title}}</div>
          <div>译者:<a href="">{{translator}}</a></div>
          <div>出版年:{{pubdate}}</div>
          <div>页数:{{pages}}</div>
          <div>定价:{{price}}</div>
          <div>装帧:{{binding}}</div>
          <div>ISBN:{{isbn}}</div>
        </div>
        
        <div>
          <h3 class="col-md-12 text-align-l margin-t10 itme-title">内容简介：</h3>
          <div class="summary col-md-12 ">
            <p class="col-md-12 text-align-l">{{summary}}</p>
          </div>
          <h3 class="col-md-12 text-align-l margin-t10 itme-title">作者简介：</h3>
          <div class="author_intro col-md-12 ">
            <p class="col-md-12 text-align-l">{{author_intro}}</p>
          </div>
          <h3 class="col-md-12 text-align-l margin-t10 itme-title">目录：</h3>
          <div class="catalog col-md-12 ">
            <p class="col-md-12 text-align-l">{{catalog}}</p>
          </div>
      </div>
      </div>
    </div>
    
  </div>
</template>

<script>
import axios from 'axios'
export default {
  name: 'bookMsg',
  /**--------------------需要绑定的数据-------------------------------*/
  data () {
    return {
      bookName:'书名',
      bookISBN:'',
      imgUrl:'图片url',
      author:'作者',
      publisher:'出版社',
      publisher2:'未知',
      origin_title:'原作名',
      pubdate:'出版日期',
      translator:'翻译',
      pages:'页数',
      price:'定价',
      isbn:'ISBN',
      binding:'装帧',
      summary:'内容简介',
      author_intro:'作者简介',
      catalog:'目录',
      books:[]
    }
  },
  /**--------------------页面加载完毕执行-------------------------------*/
  /**-----https://api.douban.com/v2/book/isbn/9787544263160-------------*/
  created:function(){
    var _this = this;
    _this.isbn= _this.$route.params.isbn;//获取传递过来的图书ISBN
    console.log(_this.isbn);
    this.$jsonp('https://api.douban.com/v2/book/isbn/'+_this.isbn)
        .then(function(info){
          _this.bookName = info.title;
          _this.author = info.author;
          _this.imgUrl = info.image;
          _this.publisher = info.publisher;
          _this.pubdate = info.pubdate;
          _this.translator = info.translator;
          _this.author = info.author;
          _this.pages = info.pages;
          _this.price = info.price;
          _this.binding = info.binding;
          _this.isbn = info.isbn13;
          _this.summary = info.summary;
          _this.author_intro = info.author_intro;
          _this.catalog = info.catalog;
          console.log(info);
        });   
  },
  methods:{
    /**--------------------按isbn搜索图书-------------------------------*/
    seachBook:function(){
      console.log('单击按钮');
      var _this = this;
      this.$jsonp('https://api.douban.com/v2/book/isbn/'+_this.bookISBN)
          .then(function(info){
            console.log(info.title);
            console.log(_this.bookName2);
          _this.bookName = info.title;
          _this.author = info.author;
          _this.imgUrl = info.image;
          _this.publisher = info.publisher;
          _this.pubdate = info.pubdate;
          _this.translator = info.translator;
          _this.author = info.author;
          _this.pages = info.pages;
          _this.price = info.price;
          _this.binding = info.binding;
          _this.isbn = info.isbn13;
          _this.summary = info.summary;
          _this.author_intro = info.author_intro;
          _this.catalog = info.catalog;
        })
          .catch(function(err){
          console.log(err);
        });
    },
    miashiti:function(){
      this.$router.push({path:'/mianShiTi'});
    }
  }
}
// https://api.douban.com/v2/book/search?q='bookname'
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
*{
  margin:0px;
  padding:0px;
}
a {
  color: #42b983;
}
.margin-t10{
  margin-top:10px
}
.fl{
  float: left;
}
.fr{
  float: right;
}

.seachbox{
  height: 40px;
  width: 100%;
}

.inputText{
  height: 40px;
  width:80%;
  border:1px solid #007fff;
  font-size: 18px;
}
.seachBtn{
  height: 40px;
  width: 60px;
  background: #007fff;
  color: #fff;
  font-size: 18px;
  border-radius: 3px;
}
.bookmsg{
  margin-top: 20px;
  padding-left: 5px;
  padding-right: 5px;
}
.bookmsglist{
  width: 70%;
  text-align: left;
  padding-left: 5px;
}
.book-name{
  text-align: left;
}
.text-align-l{
  text-align: left;
  
}
.itme-title{
  color: blue;
}
.bookimage{
  width: 30%;
  max-width: 200px;
  min-width: 50px;
  margin-top:28px;
}
.summary,.author_intro,.catalog{
  border:1px solid #ccc;
  border-radius:5px;
  background: #d9d9f3;
}
.mianshiti{
  height: 40px;
  width: 100%;
  background: #007fff;
  color: #fff;
  font-size: 18px;
  border-radius: 3px;
}
</style>



