<template>
	<div class="box">
		<el-form :model="ruleForm"  label-position="top" align="left" status-icon  :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm" size="medium">
			<el-form-item label="登陆账户" prop="account" placeholder="字母开头,6-16位">
				<el-input v-model="ruleForm.account"></el-input>
			</el-form-item>
			<el-form-item label="真实姓名" prop="chianise_name"  placeholder="中文名">
				<el-input v-model="ruleForm.chianise_name"></el-input>
			</el-form-item>
			<el-form-item label="性别" prop="sex">
				<el-radio-group v-model="ruleForm.sex">
					<el-radio label="男"></el-radio>
					<el-radio label="女"></el-radio>
				</el-radio-group>
			</el-form-item>
			<el-form-item label="出生日期" prop="age">				
				<el-col :span="20">
					<el-form-item prop="age">
						<el-date-picker type="date" placeholder="选择日期" v-model="ruleForm.age" style="width: 100%;"></el-date-picker>
					</el-form-item>
				</el-col>
			</el-form-item>
			<el-form-item label="婚姻状况" prop="marriage">
				<el-radio-group v-model="ruleForm.marriage">
					<el-radio label="已婚"></el-radio>
					<el-radio label="未婚"></el-radio>
					<el-radio label="保密"></el-radio>
				</el-radio-group>
			</el-form-item>
			<el-form-item label="身份证号码" prop="IDNumber">
				<el-input v-model="ruleForm.IDNumber" placeholder="18位"></el-input>
			</el-form-item>
			<el-form-item label="电话" prop="phone">
				<el-input v-model="ruleForm.phone" placeholder="电话号码"></el-input>
			</el-form-item>
			<el-form-item label="邮箱" prop="email">
				<el-input v-model="ruleForm.email" placeholder="邮箱"></el-input>
			</el-form-item>
			<el-form-item label="密码" prop="pass">
				<el-input type="password" v-model="ruleForm.pass" auto-complete="off"></el-input>
			</el-form-item>
			<el-form-item label="确认密码" prop="checkPass">
				<el-input type="password" v-model="ruleForm.checkPass" auto-complete="off"></el-input>
			</el-form-item>			
			<el-form-item label="爱好" prop="hobbies">
				<el-checkbox-group v-model="ruleForm.hobbies">
					<el-checkbox label="体育" name="hobbies"></el-checkbox>
					<el-checkbox label="阅读" name="hobbies"></el-checkbox>
					<el-checkbox label="演讲" name="hobbies"></el-checkbox>
					<el-checkbox label="购物" name="hobbies"></el-checkbox>
					<el-checkbox label="旅游" name="hobbies"></el-checkbox>
					<el-checkbox label="宅" name="hobbies"></el-checkbox>
					<el-checkbox label="工作狂" name="hobbies"></el-checkbox>
				</el-checkbox-group>
			</el-form-item>
			<el-form-item label="工作经验" prop="job_experience">
				<el-input type="textarea" v-model="ruleForm.job_experience"  placeholder="300字最佳"></el-input>
			</el-form-item>
			<el-form-item label="自我介绍" prop="intrduoce">
				<el-input type="textarea" v-model="ruleForm.intrduoce"  placeholder="300字最佳"></el-input>
			</el-form-item>
			<el-form-item label="公开简历" prop="open_resume">
    			<el-switch v-model="ruleForm.open_resume"></el-switch>
  			</el-form-item>

			<el-form-item>
				<el-button type="primary" @click="submitForm('ruleForm')">注 册</el-button>
				<el-button @click="resetForm('ruleForm')">重 置</el-button>
			</el-form-item>
		</el-form>			
	</div>
</template>

<script>

	import axios from 'axios'
	export default {
		data() {
			//验证账户：必须以字母开头，且以字母、数字、下划线(_)随意组合6-16位
			var validateAccount = function (rule, value, callback) {
				let reg_account= /^([a-zA-Z]{1}\w*){3,12}$/;
				if (value === '') {
					callback(new Error('请输账户'));
				}else if(!(reg_account.test(value))){
					callback(new Error('必须以字母开头，且以字母、数字、下划线(_)随意组合3-12位'));
				}else{
					callback();
				}
			};
			//验证中文名
			var validateChianiseName = function (rule, value, callback) {
				let reg_ChianiseName= /^[\u4E00-\u9FA5]{2,4}$/;
				if (value === '') {
					callback(new Error('请输中文名'));
				}else if(!(reg_ChianiseName.test(value))){
					callback(new Error('名字必须为2-4个汉字'));
				}else{
					callback();
				}
			};
			//验证身份证号码
			var validateIDNumber = function (rule, value, callback) {
				let reg_IDNumber= /(^\d{15}$)|(^\d{17})(\d|X)$/;
				if (value === '') {
					callback(new Error('请输入18位身份证'));
				}else if(!(reg_IDNumber.test(value))){
					callback(new Error('身份证必须为18位'));
				}else{
					callback();
				}
			};

			//验证密码1
			var validatePass = (rule, value, callback) => {
				let reg_pass= /^(\w){6,16}$/;
				if (value === '') {
					callback(new Error('请输入密码'));
				} else {
					if(!(reg_pass.test(value))){
						callback(new Error('密码需为6-16位任意字符'));
					}else if (this.ruleForm.checkPass !== '') {
						this.$refs.ruleForm.validateField('checkPass');
					}
					callback();
				}
			};

			//验证二次密码
			var validatePass2 = (rule, value, callback) => {
				let reg_pass2= /^(\w){6,16}$/;
				if (value === '') {
					callback(new Error('请再次输入密码'));
				}else if(!(reg_pass2.test(value))){
					callback(new Error('密码需为6-16位任意字符'));
				}else if (value !== this.ruleForm.pass) {
					callback(new Error('两次输入密码不一致!'));	
				} else {
					callback();
				}
			};
 
			//验证电话号码
			var validatePhone = function (rule, value, callback) {
				let reg_phone= /^((0\d{2,3}-\d{7,8})|(1[3584]\d{9}))$/;
				if (value === '') {
					callback(new Error('请输入电话号码'));
				}else if(!(reg_phone.test(value))){
					callback(new Error('号码格式错误，请重新确认'));
				}else{
					callback();
				}
			};
			//验证邮箱
			var validateEmail = function (rule, value, callback) {
				var reg_email=/^([a-zA-Z0-9_-])+@([a-zA-Z0-9_-])+(\.[a-zA-Z0-9_-])+/;
				if (value === '') {
					callback(new Error('请输入电子邮箱'));
				}else if(!(reg_email.test(value))){
					callback(new Error('邮箱格式错误，请重新确认'));
				}else{
					callback();
				}
			};
			return {
				ruleForm: {//绑定的数据
					account: '',
					chianise_name:'',
					sex:'',
					age:'',
					IDNumber:'',
					phone:'',
					email:'',
					marriage:'',
					pass:'',
					checkPass:'',
					hobbies:[],
					job_experience:'',
					intrduoce:'',
					open_resume:false,					
				},
				rules: {//规则
					account: [
					{ required: true, validator:validateAccount, trigger: 'blur' },
					],
					chianise_name:[
					{ required: true, validator:validateChianiseName, trigger: 'blur' },
					],
					sex:[
					{ required: true, message: '请输入性别', trigger: 'change' },
					],
					age:[
					{ type: 'date',required: true, message: '请选择出生日期', trigger: 'change' },
					],
					IDNumber:[
					{ required: true, validator: validateIDNumber, trigger: 'blur' },
					],
					phone:[
					{ required: true,validator: validatePhone, trigger: 'blur' }
					],
					email:[
					{ required: true,validator: validateEmail, trigger: 'blur' }
					],
					pass:[					
					{ required: true,validator: validatePass, trigger: 'blur' }
					],
					checkPass:[					
					{ required: true,validator: validatePass2, trigger: 'blur' }	
					],
					marriage:[
					{ required: true, message: '请选择', trigger: 'change' },
					],
					hobbies:[
					{ type: 'array', required: true, message: '请至少选择一个爱好', trigger: 'change' }
					],
					intrduoce:[
					{ required: true, message: '请输入自我介绍', trigger: 'blur' },
					],
					job_experience:[
					{ required: true, message: '请输入工作经历', trigger: 'blur' },
					],
				}
			};
		},
		methods: {
			submitForm(formName) {
				var _this = this;
				this.$refs[formName].validate((valid) => {
					if (valid) {
						let UserMsg = {
							account: this.ruleForm.account,
							chianise_name: this.ruleForm.chianise_name,
							sex: this.ruleForm.sex,
							age: this.ruleForm.age,
							IDNumber:this.ruleForm.IDNumber,
							phone:this.ruleForm.phone,
							email:this.ruleForm.email,
							marriage:this.ruleForm.marriage,
							pass:this.ruleForm.pass,
							checkPass:this.ruleForm.pass,
							hobbies:this.ruleForm.hobbies,
							job_experience:this.ruleForm.job_experience,
							intrduoce:this.ruleForm.intrduoce,
							open_resume:this.ruleForm.open_resume,
						};
						alert("注册成功！用户名为："+ _this.ruleForm.account);
						console.log(_this.ruleForm.account);
					} else {
						alert("信息不完整");
						console.log('未完整填写信息或者填写有误！请重新确认');
						return false;
					}
				});
			},
			resetForm(formName) {
				this.$refs[formName].resetFields();
			}
		}
	}
</script>


<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
	.box{
		padding-left: 5px;
		padding-right: 5px;
	}
</style>



