<template>
  <div class="hello">
    <textarea name="editor"></textarea>
    <div> 
      <form>
        <input type="file" ref="avatarInput" @change="changeImage($event)">
        <input type="button" v-on:click="edit" value="上传图片">
      </form>
    </div>


  </div>
</template>

<script>

import axios from 'axios'
export default {
  data () {
    return {
      msg: 'Welcome to Your Vue.js App',
    }
  },
  methods:{
    changeImage(e) {
      var file = e.target.files[0]
      var reader = new FileReader()
      var that = this
      reader.readAsDataURL(file)
      reader.onload = function(e) {
        that.avatar = this.result
      }
    },
    edit:function(){
     if (this.$refs.avatarInput.files.length !== 0) {
          var image = new FormData()
          image.append('img', this.$refs.avatarInput.files[0])
          axios.post('http://172.16.6.90:80/upload', image, {
          //跨域node项目地址在C:\Users\Administrator\Desktop\chenyingke\Node\exp-mode-mongose\upload-my
            headers: {
              "Content-Type": "multipart/form-data"
            }
          }).then(function(res){
            console.log("上传成功")
          }) 
        }
    } 
    
  }
}
</script>


