<template>
  <div class="container">
    <div class="seachbox"> 
        <input class="inputText fl" type="text" placeholder=" 书名、作者、ISBN" v-model="bookName_Author_ISBN">
        <input v-on:click="seachBook" class="seachBtn fl" type="button" value="搜索">
    </div>

    <div class="listBox">
      <ul class="books">
          <li v-for='book in books'>
            <a v-on:click="bookMsg(book.isbn13)" >
              <img v-bind:src="book.image" alt="">
            </a> 
            <div class="bookName">{{book.title}}</div>
            <div class="author">
              <a href="">{{book.author}}</a>
            </div>
          </li>
      </ul>
    </div>
  </div>
</template>

<script>
import axios from 'axios'
export default {
  name: 'index',
  /**--------------------需要绑定的数据-------------------------------*/
  data () {
    return {
      bookName:'书名',
      bookName_Author_ISBN:'',
      imgUrl:'图片url',
      author:'作者',
      isbn:'ISBN',
      books:[],
      booksinfo1:'',
      booksinfo2:'',
      booksinfo3:'',
    }
  },
  /**--------------------页面加载完毕执行-------------------------------*/
  created:function(){
       
  },
  methods:{
    /**--------------------按书名搜索图书-------------------------------*/
    bookMsg:function(bookisbn){
      console.log('单击跳转');
      this.$router.push({path:'/bookMsg/'+bookisbn});
    },
    seachBook:function(){
      console.log('搜索图书');
      var _this = this;

      // 书名或者作者查询
      this.$jsonp('https://api.douban.com/v2/book/search?q='+_this.bookName_Author_ISBN+'&fields=id,title,isbn13,image,author')
          .then(function(info){
            if(info.books){
              _this.books = info.books;
              console.log(info.books.length);
            }else{
              //ISBN查询    
              this.$jsonp('https://api.douban.com/v2/book/isbn/'+_this.bookName_Author_ISBN)
                  .then(function(info){
                    if(info.books){
                      _this.books = info.books;
                    }            
                    console.log(info.books.length);
                  });
            }            
            
          });

      
    }

  }
}
// https://api.douban.com/v2/book/search?q='bookname'
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
*{
  padding: 0px;
  margin:0px;
}
ul li{
  list-style: none;
}
a {
  color: #42b983;
}
li{
  margin-top: 10px;
  
  
}
.author{
  border-bottom: 1px solid #ccc;
}
li>div{
  margin-left: 5px;
  margin-right: 5px;
}
.fl{
  float: left;
}
.fr{
  float: right;
}
.seachbox{
  width: 100%;
  height: 40px;
}
.listBox{
  width: 100%;
  margin-top: 40xp;

}
.inputText{
  height: 40px;
  width:80%;
  border:1px solid #007fff;
  font-size: 18px;
}
.textBox{
  width:100%;
}
.seachBtn{
  height: 40px;
  width: 20%;
  max-width: 60px;
  background: #007fff;
  color: #fff;
  font-size: 18px;
  border-radius: 3px;
}
.book-name{
  text-align: right;
}
.text-align-l{
  text-align: right;
}
.bookimage{
  max-width: 200px;
  min-width: 50px;
}
</style>



