import Vue from 'vue'
import Router from 'vue-router'
import index from '@/components/index'
import bookMsg from '@/components/bookMsg'
import mianshiti from '@/components/mianShiTi'
import upload from '@/components/upload'
import ElementUiPage from '@/components/ElementUiPage'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'index',
      component: index
    },
    {
    	path:'/bookMsg/:isbn',
    	name:'bookMsg',
    	component:bookMsg
    },
    {
      path:'/mianShiTi',
      name:'mainshiti',
      component:mianshiti
    },
    {
      path:'/upload',
      name:'upload',
      component:upload
    },
    {
      path:'/ElementUiPage',
      name:'ElementUiPage',
      component:ElementUiPage
    }
  ]
})



